@charset "UTF-8";

/* 바텀 네비게이션 */
.bottom_navi_wrap {
  padding:vw(56) 0 0; position:relative;
  nav {
    display:flex; justify-content: space-between; position:relative; padding:vw(13) vw(10);
    &:before { width:100%; height:100%; background:url(../../images/common/bottom_navi_bg.svg) no-repeat 50% 50%; background-size:cover; position:absolute; bottom:0; left:0; content:""; border-radius:vw(16) vw(16) 0 0; }
    ul { font-size:0; @include transition(); }
    li {
      display:inline-block;
      @include opacity(.6);
      &.active { @include opacity(1); }
    }
    a {
      width:vw(120); display:block; text-align:center; padding:vw(62) 0 0; color:$white; position:relative; font-size:vw(20); line-height:vw(32); font-weight:$medium; letter-spacing:vw(-0.6);
      &:before {
        width:vw(56); height:vw(56); content:""; position:absolute; top:0; left:50%; background-position:50% 50%; background-size:cover;
        @include translate(-50%, 0);
      }
    }
    li.btn_nav_home a:before { background-image:url(../../images/common/btn_nav_home.svg); }
    li.btn_nav_member a:before { background-image:url(../../images/common/btn_nav_member.svg); }
    li.btn_nav_join a:before { background-image:url(../../images/common/btn_nav_join.svg); }
    li.btn_nav_mypage a:before { background-image:url(../../images/common/btn_nav_mypage.svg); }
  }
  .btn_request {
    width:vw(102); height:vw(102); position:absolute; top:0; left:50%; text-indent:-99999px; background:url(../../images/common/btn_request.svg) no-repeat 50% 50%; background-size:cover; border-radius:100%; z-index:101;
    @include translate(-50%, 0);
    @include transition();
  }
  .bottom_navi_bg_wrap { width:0; height:0; }
  .request_list_wrap {
    width:100%; height:100%; position:fixed; top:0; left:0; background:rgba(0, 0, 0, 0.6); z-index:100;
    @include opacity(0); visibility:hidden;
    @include transition();
    ul {
      position:absolute; bottom:vw(256); left:0; width:100%; box-sizing:border-box; padding:0 vw(76);
      li {
        margin:vw(16) 0 0; background-color:$white; border-radius:vw(16);
        @include translate(-100%, 0);
        @include transition();
        @include opacity(0);
        &:first-child { margin:0; }
        a {
          display:block; padding:vw(34) vw(40) vw(34) vw(120); font-size:vw(28); line-height:vw(56); letter-spacing:vw(-0.6); font-weight:$medium; position:relative;
          &:before,&:after {
            width:vw(56); height:vw(56); position:absolute; top:50%; content:""; background-size:cover;
            @include translate(0, -50%);
          }
          &:before { left:vw(40); background-position:50% 50%; background-repeat:no-repeat; }
          &:after { right:vw(40); background:url(../../images/common/icon_arrow2.svg) no-repeat 50% 50%; }
        }
        &:nth-of-type(1),&:nth-of-type(2),&:nth-of-type(3) { transition-delay:0s }
        &.btn_request_field a:before { background-image:url(../../images/common/icon_field.svg) }
        &.btn_request_screen a:before { background-image:url(../../images/common/icon_screen.svg) }
        &.btn_request_field_match a:before { background-image:url(../../images/common/icon_field_match.svg) }
        &.btn_request_screen_match a:before { background-image:url(../../images/common/icon_screen_match.svg) }
      }
    }
  }

  // 모집 및 매칭 활성화
  &.navi_active {
    nav {
      &:before { background-image:url(../../images/common/bottom_navi_bg_gray.svg) }
      ul { visibility:hidden; @include opacity(0); }
    }
    .btn_request {
      background-image:url(../../images/common/btn_request_close.svg); background-color:$green1;
      @include transform(translate(-50%, 0) rotate(90deg));
    }
    .request_list_wrap {
      visibility:visible;
      @include opacity(1);
      ul li {
        @include opacity(1);
        @include translate(0, 0);
        &:nth-of-type(1) { transition-delay:0.425s; }
        &:nth-of-type(2) { transition-delay:0.35s; }
        &:nth-of-type(3) { transition-delay:0.275s; }
        &:nth-of-type(4) { transition-delay:0.2s; }
      }
    }
  }
}

/* 페이지 타이틀 */
.page_wrap {
  position:relative; height:vw(120);
  h2 { width:100%; font-size:vw(34); line-height:vw(120); letter-spacing:vw(-0.6); color:$gray9; font-weight:$medium; position:absolute; top:0; left:0; text-align:center; }
  .btn_align_left, .btn_align_right {
    position:absolute; top:50%; font-size:0; z-index:1;
    @include translate(0,-50%);
  }
  .btn_align_left { left:vw(9); }
  .btn_align_right { right:vw(9); }
  button {
    width:vw(72); height:vw(72); margin:0 vw(5); text-indent:-999999px; background-position:0 0; background-size:cover; background-repeat:no-repeat;
    &.btn_page_prev { background-image:url('../../images/common/btn_page_prev.svg') }
    &.btn_page_prev2 { background-image:url('../../images/common/btn_page_close.svg') }
    &.btn_alarm {
      background-image:url('../../images/common/btn_alarm.svg'); position:relative;
      &.active:after { width:vw(10); height:vw(10); content:""; position:absolute; top:vw(14); right:vw(14); border-radius:100%; background-color:$red; }
    }
    &.btn_nav { background-image:url('../../images/common/btn_nav.svg') }
  }
}

/* 안내 문구 */
.notification_type01 {
  background-color:$gray05; padding:vw(40); border-radius:vw(16);
  li {
    font-size:vw(24); line-height:vw(34); letter-spacing:vw(-0.6); margin:vw(16) 0 0; position:relative; padding:0 0 0 vw(20);
    &:first-child { margin:0; }
    &:before { width:vw(8); height:vw(8); background-color:$gray4; border-radius:100%; position:absolute; top:vw(12); left:0; content:""; }
  }
}

.notification_type02 {
  font-size:vw(24); line-height:vw(38); letter-spacing:vw(-0.6); color:$gray5; position:relative; padding:0 0 0 vw(48);
  &:after { width:vw(36); height:vw(36); content:""; background:url(../../images/common/icon_info.svg) no-repeat 0 0; background-size:cover; position:absolute; top:0; left:0; }
}

.notification_type03 {
  background-color:#fef2f2; border-radius:vw(16); padding:vw(26) 0; text-align:center;
  span {
    display:inline-block; padding:0 0 0 vw(46); position:relative; font-size:vw(24); line-height:vw(36); color:$red; letter-spacing:vw(-0.6);
    &:before { width:vw(36); height:vw(36); content:""; background:url(../../images/common/icon_alert_red.svg) no-repeat 0 0; background-size:cover; position:absolute; top:0; left:0; }
  }
}

/* 내정보 */
.my_profile_ui {
  display:flex;
  .picture_wrap {
    width:vw(130); margin:0 vw(32) 0 0; position:relative;
    span { display:block; width:vw(130); height:vw(130); text-indent:-99999px; background:url(../../images/common/no_image.svg) no-repeat 50% 50% $gray2; background-size:cover; border-radius:100%; overflow:hidden; }
    /*button {
      width:vw(54); height:vw(54); position:absolute; bottom:0; right:vw(-4); background:url(../../images/common/icon_upload.png) no-repeat 50% 50% #f1f3f5; background-size:cover; border-radius:100%; text-indent:-99999px; box-siizng:border-box;
      &:after { width:100%; height:100%; border:vw(6) solid $white; border-radius:100%; position:absolute; top:0; left:0; content:""; box-sizing:border-box; }
    }*/
  }
  .info_wrap {
    width:calc(100% - 18.05556vw);
    .user_label {
      display:inline-block; margin:0 0 vw(7); font-size:vw(20); line-height:vw(33); padding:0 vw(12); border-radius:vw(8); font-weight:$medium; color:$white;
      &.label_normal { background-color:$green3; }
      &.label_vip { background-color:$violet1; font-weight:$bold; }
    }
    .user_name { display:block; font-size:vw(32); line-height:vw(46); font-weight:$medium; letter-spacing:vw(-0.6); }
    .user_info {
      font-size:0;
      ul {
        font-size:0; display:inline-block; margin:0 0 0 vw(12); padding:0 0 0 vw(20); position:relative;
        &:first-child { margin:0; padding:0; }
        &:first-child:before { display:none; }
        &:before { content:"/"; font-size:vw(20); color:$gray4; font-weight:$bold; position:absolute; top:vw(2); left:0; }
        li {
          display:inline-block; font-size:vw(24); font-weight:$medium; color:$gray5; position:relative; padding:0 0 0 vw(12); margin:0 0 0 vw(6);
          &:first-child { margin:0; padding:0; }
          &:first-child:before { display:none; }
          &:before { content:""; width:vw(6); height:vw(6); background-color:$gray2; border-radius:100%; position:absolute; top:vw(8); left:0; }
        }
      }
    }
  }
}